@import 'constants';

.contact_container{
    width: 100%;
    background-color: $accentElement;
    padding: 10px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; 
    gap: 15px;

    p {
        font-size: 12px;
        color: $textColor;
        font-family: 'Inter', sans-serif;
    }
}

.contact_item_container
{
    display: flex;
    gap: 30px;
    
    img {
        width: 20px;
    }
}